  z-index: 999;
}
@media (min-width: 768px) {
  .js .main-nav__wrapper {
    position: static;
    top: auto;
    left: auto;
    width: auto;
    background-color: transparent;
    z-index: 0;
  }
}
.main-nav__logo {
  display: flex;
