  margin: auto;
  display: block;
}
@media (min-width: 768px) {
  .main-nav__img {
    position: static;
    width: 174px;
  }
}
@media (min-width: 1440px) {
  .main-nav__img {
    width: 202px;
  }
}
@media (min-width: 768px) {
  .main-nav__image {
    display: none;
  }
}
.main-nav__toggle {
  position: absolute;
  padding: 0;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  cursor: pointer;
  z-index: 2;
  right: 0;
  top: 0;
  height: 100%;
  width: calc(12.5% + 24px);
}
.main-nav__toggle::after {
  content: "";
  position: absolute;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 24px;
  transform: rotate(0deg);
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  left: 0;
  right: 0;
  margin: auto;
  top: 22px;
  box-shadow: 0 8px 0 0 #000000;
  transform: rotate(0deg);
}
.main-nav--show .main-nav__toggle::after {
  box-shadow: none;
  transform: rotate(45deg);
  top: 30px;
  bottom: auto;
}
.main-nav--show .main-nav__toggle::before {
  box-shadow: none;
  transform: rotate(-45deg);
  top: 30px;
  bottom: auto;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
  }
}
@media (max-width: 767px) {
  .main-nav--show .main-nav__list {
    display: block;
  }
  .main-nav--hide .main-nav__list {
    display: none;
  }
}
